package com.courseexplorer

import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.getValue
import androidx.compose.runtime.remember
import androidx.compose.runtime.setValue
import kotlinx.browser.document
import org.jetbrains.compose.common.material.Button
import org.jetbrains.compose.web.attributes.InputType
import org.jetbrains.compose.web.attributes.placeholder
import org.jetbrains.compose.web.css.*
import org.jetbrains.compose.web.css.selectors.type
import org.jetbrains.compose.web.dom.*
import org.jetbrains.compose.web.renderComposable

fun main() {
    val viewModel = HomeSearchViewModel()
    var count: Int by mutableStateOf(0)
    var searchTerm : String by  mutableStateOf("")
    renderComposable(rootElementId = "root") {

        Div({
            classes("topnav")
        }) {
            Input(type = InputType.Text){
                id("searchInput")
                value(searchTerm)
                placeholder("Enter Search Term.....")
                onInput {
                    searchTerm = it.value
                }
                style {
                    width(600.px)
                    height(30.px)
                }
            }
            Button({ onClick { searchTerm = "" } }){
                Text("Clear")
            }

            Button({onClick { }}){
                Text("Submit")
            }
        }


    }
}